.dialogs{
	display: grid;
	grid-template-columns: 2fr 4fr;
	column-gap: 30px;
	background-color: rgba(16, 58, 80, 0.6);
	min-height: 100%;
    padding: 20px 10px;
	padding-right: 20px;
	position: relative;
}
.dialogs::after {
	content: "";
	background: url("https://games.mail.ru/pic/pc/gallery/91/36/ce0f987a.jpeg");
	opacity: 1;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	position: absolute;
	z-index: -1;  
  }
.dialogsList{}

